.App {
  width: 100%;
  height: 100%;
  display: flex;
  overflow: hidden;

}

.Intro {
  height: 100%;
  background: #2bc0e4; /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #2bc0e4, #eaecc6); /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(30deg, #d4fff4 ,#f8d5ff); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  width: 40%;
}

.Right {
  background-color: #282c34;
  height: 100%;
  width: 60%;
}

@media (max-aspect-ratio: 12/9){
    .App{
      display: initial;
    }
    .Intro,.Right{
      width: 100%;
    }
}


